@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  body {
    @apply bg-dark-100 text-gray-200;
  }

  /* Hide scrollbar for Chrome, Safari and Opera */
  ::-webkit-scrollbar {
    display: none;
  }

  /* Hide scrollbar for IE, Edge and Firefox */
  html {
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
  }
}

@layer components {
  .bg-gradient-dark {
    @apply bg-gradient-to-br from-dark-100 to-dark-200;
  }

  .text-gradient {
    @apply bg-clip-text text-transparent bg-gradient-to-r from-primary-300 to-secondary-100;
  }

  /* Text color utilities */
  .text-default {
    @apply text-gray-200;
  }

  .text-muted {
    @apply text-gray-400;
  }

  .text-strong {
    @apply text-white;
  }
}

@layer utilities {
  .shadow-glow-sm {
    box-shadow: 0 0 10px -1px rgba(56, 189, 248, 0.3);
  }
  .shadow-glow-md {
    box-shadow: 0 0 20px -1px rgba(56, 189, 248, 0.4);
  }
  .shadow-glow-lg {
    box-shadow: 0 0 30px -1px rgba(56, 189, 248, 0.5);
  }
  .text-shadow-sm {
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
  }
  .text-shadow-md {
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
  }
  .text-shadow-lg {
    text-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  }
  .backdrop-blur-xs {
    backdrop-filter: blur(2px);
  }
}

@keyframes float {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

.animate-float {
  animation: float 6s ease-in-out infinite;
}

@keyframes gradient-x {
  0%,
  100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}

.animate-gradient-x {
  animation: gradient-x 15s ease infinite;
  background-size: 200% 200%;
}
